.content {
  border-radius: 20px;
  padding: 20px;
  background-color: rgb(237, 237, 243);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4196078431);
  max-width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  gap: 20px;
  margin-bottom: 20px;
  overflow: hidden;
  align-items: center;
}

.header {
  font-size: 28px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.inputLogin,
.inputPassword {
  font-size: 22px;
  padding: 5px 10px;
  border-radius: 20px;
}
.inputLogin::selection,
.inputPassword::selection {
  background-color: rgba(69, 77, 94, 0.768627451);
  color: white;
}

.inputRememberContainer {
  display: flex;
  justify-content: flex-start;
  gap: 10px;
  font-size: 18px;
}

.inputRemember {
  flex: 0 0 auto;
}

.submitContainer {
  display: flex;
  justify-content: center;
}

.submit {
  flex: 1 0 auto;
  overflow: hidden;
  border: solid 1px #cfcfcf;
  padding: 7px 15px;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 2px;
  border-radius: 20px;
  background-color: #fff;
  color: #000;
  transition: all 0.2s ease-in-out;
}
.submit:hover {
  border: solid 1px #454d5e;
  background-color: #454d5e;
  color: #fff;
  transition: all 0.1s ease-in-out;
}
.submit:disabled {
  opacity: 0.7;
}

.summaryError {
  text-align: center;
  font-size: 18px;
  padding: 7px 10px;
  background-color: #fff;
  border-radius: 20px;
  border: 1px solid red;
  color: red;
}